import React, { useState } from "react";
import {
  displayErrorToast,
  displaySuccessToast,
  axiosInstance,
} from "./Constants";
import ProgressBar from "./ProgressBar";

const DisplayTable = (props) => {
  const selectedFiles = props.data;
  return (
    <div style={{ width: "80%", margin: "20px 0px" }}>
      <h5>Selected Files:</h5>
      <table>
        <thead>
          <tr>
            <th>File Name</th>
            <th>Size (Kb)</th>
            <th>Status</th>
          </tr>
        </thead>
        <tbody>
          {selectedFiles.length > 0 &&
            selectedFiles.map((file, index) => {
              return (
                <tr key={index}>
                  <td>{file.name}</td>
                  <td>{file.size / 1024}</td>
                  <td>
                    {`${file.progress || 0}%  `}
                    <ProgressBar percentage={file.progress || 0} />
                  </td>
                </tr>
              );
            })}
        </tbody>
      </table>
    </div>
  );
};

const UploadFiles = () => {
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [progress, setProgress] = useState();
  const [error, setError] = useState();

  const fileValidations = () => {
    setError("");
    let errMsg;
    if (selectedFiles.length <= 0) {
      errMsg = "Please select a file before uploading!";
    }
    let totalSize = 0;
    selectedFiles.forEach((currentFile) => {
      totalSize += currentFile.size || 0;
    });

    if (totalSize > 209715201) {
      errMsg = "File size is too large. Please upload files below 200MB!";
    }

    if (errMsg) {
      displayErrorToast(errMsg);
      setError(errMsg);
      return false;
    }

    return true;
  };

  const submitHandler = (evt) => {
    evt.preventDefault();

    if (fileValidations()) {
      const fileList = [...selectedFiles];
      fileList.forEach((currentFile, index) => {
        const formData = new FormData();
        formData.append("UserId", "abc.xyz@abc.com");
        formData.append("file", currentFile);

        axiosInstance
          .post("/fileupload", formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
            onUploadProgress: (data) => {
              //Set the progress value to show the progress bar
              const pg = Math.round((100 * data.loaded) / data.total);
              setProgress(pg);
              currentFile.progress = pg;
              setSelectedFiles([...fileList]);
            },
          })
          .catch((error) => {
            displayErrorToast(
              `Failed to upload file ${
                currentFile ? currentFile.name : ""
              } with error ${error}`
            );
          });
      });

      displaySuccessToast(`Files uploaded successfully`);

      setSelectedFiles([]);
    }
    evt.target.reset();
  };

  return (
    <div style={{ margin: "20px" }}>
      <form
        method="post"
        encType="multipart/form-data"
        onSubmit={submitHandler}
      >
        <div className="button-green">
          <label
            style={{
              display: " inline-block",
            }}
          >
            <input
              type="file"
              id="fileUpload"
              name="file"
              title=" "
              multiple={true}
              onChange={(evt) => {
                const newSelectedFiles = [
                  ...selectedFiles,
                  ...evt.target.files,
                ];
                console.log("selectedFiles", newSelectedFiles);
                setSelectedFiles(newSelectedFiles);
              }}
              style={{ display: "none" }}
            />
            Select Files
          </label>
        </div>
        <div>
          {!error && progress > 0 && (
            <>
              {`${progress}%`} <ProgressBar percentage={progress || 0} />
            </>
          )}
        </div>
        <div>
          <DisplayTable data={selectedFiles} />
        </div>
        <div>
          <button
            type="button"
            variant="info"
            type="submit"
            className="button-grey"
            style={{ marginRight: "50px" }}
          >
            Upload
          </button>
          <button
            type="reset"
            className="button-grey"
            onClick={() => {
              setSelectedFiles([]);
              setProgress(0);
            }}
          >
            Reset
          </button>
        </div>
      </form>
    </div>
  );
};

export default UploadFiles;
