import React from 'react';
import { Link } from 'react-router-dom';
import Dropzone from 'react-dropzone-uploader';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faThList } from '@fortawesome/free-solid-svg-icons';
import UploadFiles from './UploadFiles';

const UploadDocument = () => {
  const getUploadParams = ({ meta }) => {
    const url = 'http://localhost:9000/fileupload';
    return {
      url,
      meta: {
        fileUrl: `${ url }/${ encodeURIComponent(meta) }`,
      },
    };
  };

  const handleChangeStatus = ({ meta }, status) => {
    console.log('handleChangeStatus');
    console.log(status, meta);
  };

  const handleSubmit = (files, allFiles) => {
    console.log('handleSubmit');
    console.log(files.map((f) => f.meta));
    allFiles.forEach((f) => f.remove());
  };

  return (
    <div
      style={{
        height: '100%',
        width: '1049px',
        overflow: 'auto',
      }}
    >
      <div>
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginLeft: '19px',
            marginRight: '10px',
          }}
        >
          <p>&nbsp; </p>
          <h2>Upload Document</h2>
          <span>
            <Link to="/documents/view">
              <FontAwesomeIcon icon={faThList} color="black" size="lg" />
              <span> Document List</span>
            </Link>
          </span>
        </div>
      </div>
      <div>
        {/* <Dropzone
          getUploadParams={getUploadParams}
          onChangeStatus={handleChangeStatus}
          onSubmit={handleSubmit}
          //accept="image/*,audio/*,video/*"
          inputContent={(files, extra) =>
            extra.reject ? "Image, audio and video files only" : "Drag Files"
          }
          styles={{
            dropzoneReject: {
              borderColor: "red",
              backgroundColor: "#DAA",
            },
            inputLabel: (files, extra) =>
              extra.reject
                ? {
                    color: "red",
                  }
                : {},
          }}
        /> */}
        <UploadFiles />
      </div>
    </div>
  );
};

export default UploadDocument;
