import React from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCoffee,
  faHome,
  faFile,
  faUpload,
} from '@fortawesome/free-solid-svg-icons';

const MenuBar = () => {
  return (
    <ul>
      <li>
        <Link to="/">
          <FontAwesomeIcon
            icon={faHome}
            color="black"
            size="lg"
            className="fa-fw"
          />

          <span>Home</span>
        </Link>
      </li>

      <li>
        <Link to="/counter">
          <FontAwesomeIcon
            icon={faCoffee}
            color="black"
            size="lg"
            className="fa-fw"
          />

          <span>Counter</span>
        </Link>
      </li>

      <li>
        <Link to="/documents/view">
          <FontAwesomeIcon
            icon={faFile}
            color="black"
            size="lg"
            className="fa-fw"
          />

          <span>Documents</span>
        </Link>
      </li>

      <li>
        <Link to="/documents/upload">
          <FontAwesomeIcon
            icon={faUpload}
            color="black"
            size="lg"
            className="fa-fw"
          />

          <span>Upload</span>
        </Link>
      </li>
    </ul>
  );
};

export default MenuBar;
